function setSelectedGameVersion(btn) {
    let ver = btn.dataset.value;
    let div = document.getElementById("game-version-cache");
    div.innerHTML = ver;
    div = document.getElementById("mod-version-list-" + ver);
    let box = document.getElementById("modver-box");
    box.innerHTML = "";
    div.scrollTop = 0;
    var children = div.children;
    for (var i = 0; i < children.length; i++) {
        let entry = children[i];
        let name = entry.innerHTML;
        let button = document.createElement("button");
        button.type = "button";
        button.className = "verbtn-mod";
        button.dataset.value = name;
        button.dataset.date = entry.dataset.date;
        button.innerHTML = '<span><div class = "modver-width">' + name + '</div><i class="fas fa-angle-right"></i></span>';
        button.addEventListener("click", function () { setSelectedModVersion(button); });
        box.appendChild(button);
        //box.innerHTML += '<button type="button" class = "verbtn-mod" data-value = "' + name + '" data-date = "' + entry.dataset.date + '" onclick = "setSelectedModVersion(this)"><span><div class = "modver-width">' + name + '</div><i class="fas fa-angle-right"></i></span></button><br>'
    }
    var list = document.getElementsByClassName("selver-game");
    for (var i = 0; i < list.length; i++) {
        list[i].classList.remove("selver-game");
    }
    btn.classList.add("selver-game");
    document.getElementById("changedata").innerHTML = document.getElementById("changedata-empty-cache").innerHTML;
    document.getElementById("changedata-header").innerHTML = "Changes";
}
function setSelectedModVersion(btn) {
    //if (btn == null)
    //	return;
    let ver = btn.dataset.value;
    let div = document.getElementById("mod-version-cache");
    div.innerHTML = ver;
    div.dataset.date = btn.dataset.date;
    if (div.dataset.date != "")
        div.dataset.date = " (" + div.dataset.date + ")";
    //console.log(ver+" on "+div.dataset.date);
    var list = document.getElementsByClassName("selver-mod");
    for (var i = 0; i < list.length; i++) {
        list[i].classList.remove("selver-mod");
    }
    btn.classList.add("selver-mod");
    updateChangeData();
}
function updateChangeData() {
    let text = document.getElementById("changedata");
    let gv = document.getElementById("game-version-cache");
    let mv = document.getElementById("mod-version-cache");
    let gameVer = gv.innerHTML;
    let modVer = mv.innerHTML;
    if (modVer == null || modVer == "" || typeof (modVer) == "undefined")
        return;
    // text.innerHTML = "<h2 class = \"changelog-version-title\">"+modVer+" ["+gameVer+"]"+mv.dataset.date+"</h2>";
    text.innerHTML = "";
    let header = document.getElementById("changedata-header");
    header.innerHTML = "Changes in " + modVer + " [" + gameVer + "]" + mv.dataset.date + ":";
    var list = document.getElementsByClassName("modchangecheck");
    for (var i = 0; i < list.length; i++) {
        let obj = list[i];
        if (obj.checked == true) {
            var mod = obj.dataset.value;
            var key = gameVer + "|" + modVer + "|" + mod;
            var div = document.getElementById(key);
            //console.log("'"+key+"' > "+div);
            if (div != null) {
                text.innerHTML += "<h3>" + obj.dataset.mod + "</h3>" + div.innerHTML + "<br>";
            }
        }
    }
}
export default function init() {
    var btns = document.getElementsByClassName("modchangecheck");
    for (var i = 0; i < btns.length; i++) {
        let btn = btns[i];
        btn.addEventListener("click", function () { updateChangeData(); });
    }
    btns = document.getElementsByClassName("verbtn-game");
    for (var i = 0; i < btns.length; i++) {
        let btn = btns[i];
        btn.addEventListener("click", function () { setSelectedGameVersion(btn); });
    }
    //setSelectedModVersion(null);
}
